/* Background Image */
.bgimage {
  position: relative;
}

.bgimage:before {
  z-index: 1;
}

.content_above {
  position: relative;
  z-index: 2;
}

/* Background image holder */
.bg_image_holder {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat;
  z-index: 0;
  transition: opacity 0.3s linear;
  -webkit-transition: opacity 0.3s linear;
  opacity: 0;
}

.bg_image_holder img {
  display: none;
}

/* overlays */
.overlay {
  position: relative;
}

.overlay:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.overlay.overlay--dark:before {
  background: rgba(47, 38, 57, 0.8);
}

